 */


@import '../../../template.scss';

.review {
	@include main-container();

	border-radius: size(25px);
	background-color: bisque;
	padding-top: size(165px) !important;
	padding-bottom: size(117px) !important;
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;
}

.review-section-content {
	display: flex;
	justify-content: space-between;
	margin-top: size(51px);
	align-items: end;
	margin-bottom: size(112px);

}

.review .section__sub .section__line {
	background-color: rgba(255, 255, 255, 0.4);
}

.review .section__sub .section__text,
.review .section__sub .section__number {
	color: rgba(255, 255, 255, 0.7) !important;
	opacity: 1;
}


.review-section-title {
	margin: 0;
	font-size: size(40px);
	width: size(530px);
	color: #fff;
	overflow: hidden;
	line-height: 120%;
	font-weight: 500;
}

.review-section-desc-con {
	display: flex;
}

.review-section-description {
	width: size(480px);
	font-size: size(16px);
	line-height: 160%;
  text-align: right;
	color: rgba(255, 255, 255, 0.4);
}

.review-section-desc-con svg {
	margin-left: size(26px);
	width: size(56px);
	height: size(56px);
}



.review-section-img {
	position: relative;
}

.review-section-img img {
	position: absolute;
	bottom: size(-329px);
	right: size(-303px);
}



//SLIDER
.slick-slider {
	display: flex !important;
	justify-content: center;
	transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
	/* Плавное изменение размеров */
	overflow: visible;
	/* Убедитесь, что элементы не обрезаются */
	align-items: center;

}

.slick-slider .slick-arrow svg {
	width: size(44px) !important;
	height: size(44px) !important;
}

.slick-slider .slick-arrow:nth-child(1) {
	margin-right: size(27px);
}

.slick-slider .slick-arrow:last-child {
	margin-left: size(27px);
}

.slick-track {
	transition: transform 0.5s ease-in-out;
	/* Плавность переключения */
	height: size(445px) !important;
}

.slider-item {
	position: relative;
	width: 100% !important;
	max-width: size(190px);
	/* Предельная ширина карточки */
	height: size(298px) !important;
	border-radius: size(12px);
	overflow: hidden;
	background: #000;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: size(23px);
	transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
	/* Плавность размеров */
}


.slick-initialized .slick-slide {
	transition: 0.5s;
	margin-top: size(60px);
}




.slick-initialized .slick-slide .play-button {
	display: none;

}

.slick-initialized .slick-slide .review-video {
	pointer-events: none;
}

.slick-track .slick-active .play-button {
	display: block;
}

.slick-track .slick-active .review-video {
	pointer-events: all;
}




.slick-track .slick-active {
	width: 100% !important;
	max-width: size(285px);
	/* Предельная ширина карточки */
	height: size(445px) !important;
	margin-top: 0px;
	border: 2px solid white;
}

.slider-item .review-video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	pointer-events: all;
	/* Запрещаем взаимодействие с видео */
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(to bottom, transparent 60%, rgba(0, 0, 0, 0.8) 100%);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	pointer-events: none;
	/* Чтобы только кнопка срабатывала */
}

.play-button {
	width: size(50px);
	height: size(50px);
	mask-size: contain;
	-webkit-mask-size: contain;
	pointer-events: all;
	/* Активируем клики */
	cursor: pointer;
}

.video-info {
	position: absolute;
	bottom: size(25px);
	left: size(25px);
	color: white;
	font-family: Arial, sans-serif;
	pointer-events: none;

}

.video-info h3 {
	margin: 0;
	font-size: size(18px);
	font-weight: bold;
}

.video-info p {
	margin: size(13px) 0 0;
	font-size: size(14px);
	color: rgba(255, 255, 255, 0.5);
}

.play-button:hover {
	opacity: 0.8;
}



.slick-slider {}

//slick
.slick-dots .slick-active button {}

.block-editor-block-list__block .slick-slider .slider-item {
	display: flex;
	flex-direction: column;
}




.custom-dots-container {
	position: relative;
}

.slick-dots .slick-active {
	background: white;
	width: size(34px) !important;
	height: size(7px) !important;
	border-radius: 100px;
}

.slick-dots li button:before {
	color: #D9D9D9 !important;
}

.slick-dots .slick-active button:before {
	display: none;
}

.slick-dots li button {
	height: size(7px) !important;
	padding: 0 !important;
	width: size(15px) !important;
}

.slick-dots li button:before {
	line-height: size(7px) !important;
	height: size(7px) !important;
	font-size: size(10px) !important;
	width: size(15px) !important;
}


.custom-dots-container div {
	position: relative;
}


.slick-slider .slick-track {
	//transform: translate3d(0px, 0px, 0px) !important;
}

.custom-dots-container {
	margin-top: size(52px);
}




//MOBILE

@media (max-width: 600px) {

	.review {
		padding-top: 87px !important;
	}

	.review-section-content {
		margin-top: size(100px);
		flex-direction: column;
		margin-bottom: size(172px);
	}

	.review-section-img {
		display: none;
	}

	.review-section-title {
		font-size: 28px;
		width: 100%;
	}

	.review-section-desc-con svg {
		display: none;
	}

	.review-section-description {
		width: 100%;
		margin-top: 46px;
		font-size: 16px;
	}


	.slick-dots .slick-active {
		background: white;
		width: 34px !important;
		height: 7px !important;
		border-radius: 100px;
	}

	.slick-dots li button:before {
		color: #D9D9D9 !important;
	}

	.slick-dots .slick-active button:before {
		display: none;
	}

	.slick-dots li button {
		height: 7px !important;
		padding: 0 !important;
		width: 15px !important;
	}

	.slick-dots li button:before {
		line-height: 7px !important;
		height: 7px !important;
		font-size: 10px !important;
		width: 15px !important;
	}




	.slick-dots li {
		width: 7px !important;
		height: 7px !important;
	}

	.slick-arrow {
		display: none !important;
	}




	.slider-item {
		max-width: 224px;
		/* Предельная ширина карточки */
		height: 351px !important;
		margin-right: 23px;
		border-radius: 18px;

	}

	.slick-initialized .slick-slide {
		margin-top: 60px;
	}

	.slick-track {
		height: 445px !important;
	}

	.slick-track .slick-active {
		width: 100% !important;
		max-width: 285px;
		/* Предельная ширина карточки */
		height: 445px !important;
		border-radius: 18px;
		margin-top: 0;
	}

	.video-info {
		bottom: 25px;
		left: 25px;
	}

	.video-info h3 {
		font-size: 18px;
		margin-bottom: 19px;
	}

	.video-info p {
		font-size: 14px;
	}

	.review {
		padding-bottom: 85px !important;
	}
}
